@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap');

* {
    padding: 0px;
    padding-top: 0px;
    margin: 0px;
}
body{
    width: auto;
}   

// p√°gina principal
nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    background-color: #317B00;
    width: auto;
    height: 140px;
    color: white;
    font-size: 45px;

    h1 {
        padding-right: 80px;
    }

    img {
        width: 60px;
        height: 60px;
        padding-left: 80px;
    }

    a {
        font-size: medium;
        position: absolute;
        top: 61px;
        left: 138px;
        color: white;
        text-decoration: none;
    }
}

main {
    padding: 0 100px 0 100px;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    font-size: 20px;
    gap: 130px;

    p {
        margin-bottom: 70px;
    }

    h1 {
        font-size: 40px;
        line-height: 80px;
    }

    aside {
        img {
            height: 300px;
            margin-bottom: 50px;
        }
    }
    
}


footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    font-family: 'Montserrat', sans-serif;
    height: 75px;
    width: auto;
    background-color: #317B00;
    a{
        color: white;
        text-decoration: none;
    }
}